	module.exports = {"data":{"markdownRemark":{"html":"<h3>Описание:</h3>\n<p>Одно из основных упражнений для мышц груди. Разведение рук с гантелями придает груди рельефность и выпуклую форму.</p>\n<p>Выполняется упражнение как на горизонтальной скамье, так и на наклонной, что позволяет сделать акцент на разные области грудных мышц:</p>\n<ul>\n    <li>верхнюю (если поднять головной конец скамьи),</li>\n    <li>среднюю (горизонтально ровная скамья),</li>\n    <li>нижнюю (если опустить головной конец скамьи).</li>\n</ul>\nДля безопасного и правильного выполнения разведения рук с гантелями необходимо отрегулировать высоту скамьи так, чтобы ступнями вы надежно упирались в пол, для избежания потери равновесия.\n<h3>Порядок выполнения:</h3>\n<ol>\n    <li>Держа гантели в руках, лягте на скамью, ноги уприте в пол.</li>\n    <li>Исходное положение: поднимите гантели вверх. Руки немного согнуты в локтях. Угол в локтевом суставе необходимо зафиксировать и держать неизменным во время выполнения упражнения.</li>\n    <li>На вдохе – по широкой дуге разведите руки в стороны только за счет плечевого сустава. Опускайте гантели до уровня плеч или немного ниже.</li>\n    <li>На выдохе – усилием мышц груди сведите гантели над собой по обратной траектории.</li>\n    <li>Выполните необходимое количество повторений.</li>\n</ol>\n<h3>Советы:</h3>\n<ul>\n  <li>Следите, чтобы руки были немного согнуты (примерно 150 градусов) – так упражнение будет максимально эффективно и безопасно.</li>\n  <li>Локти, во время разведения рук, должны смотреть вниз, а не вперёд, Иначе нагрузка будет ложиться не плечи, что может привести к травме плечевых суставов.</li>\n  <li>Не используйте тяжелые гантели – вес должен быть такой, чтобы вы смогли выполнить хотя бы 10 повторений. Это поможет избежать травмы плечевых суставов.</li>\n  <li>Для максимального эффекта растяжения грудных мышц следите за положением локтей. Чем локти ниже, тем сильнее растянуты мышцы груди. При этом, для избежания травм, не опускайте гантели ниже уровня плечей.</li>\n  <li>Не забывайте про уровень наклона скамьи, чтобы сделать акцент на разные области грудных мышц.</li>\n</ul>\n<h3>Видео-инструкция:</h3>\n<iframe width=\"100%\" height=\"550rem\" src=\"https://www.youtube.com/embed/iurQxojfNpk\" frameborder=\"0\" allow=\"autoplay; encrypted-media\" frameborder=\"0\" allow=\"autoplay; encrypted-media\" allowfullscreen></iframe>","frontmatter":{"path":"/exercise/razvedenie-ruk-s-gantelyami-leja","efficiency":"9","title":"Разведение рук с гантелями лежа","image":"/static/0-02-0.png","image_2":"/static/0-02-1.jpg","image_3":"/static/0-02-2.jpg","image_4":"","filterby":"chest","date":"July 12, 2017","injury":"6","bodypart":"Грудь","filtertype":"isolated","type":"Изолирующее","filterinventory":"dumbbells","inventory":"Гантели","filterplace":""}}},"pathContext":{}}

